<template>
    <div class="about-data">
        <navigate title="个人资料" position="absolute"/>
        <div class="scroll-wrapper">
            <div class="form">
                <!-- 头像 -->
                <div class="form-item" style="height: auto;padding: 15px">
                    <div class="form-item-label">
                        <mu-avatar size="80" @click.native="handlerClickAvatar">
                            <img v-lazy="AvatarImage($images + (data.portrait || user.portrait))">
                        </mu-avatar>
                    </div>
                    <div class="form-item-content">
                        <span>{{data.alias || user.alias}}</span>
                    </div>
                    <div class="form-item-other">
                        <mu-button color="#477FB9" @click="handlerSaveInfo" flat>个人资料</mu-button>
                    </div>
                </div>
                <!-- 头像 -->
            </div>
            <div class="form">
                <swipe-cell class="border-1px-b" v-for="(item, index) in data.shipList" :key="item.id"
                            :right-width="208">
                    <div class="form-item">
                        <div class="form-item-label">
                            船舶{{index + 1}}
                        </div>
                        <div class="form-item-content">{{item.name}}</div>
                        <div class="form-item-other" v-if="item.default">
                            <span style="color: #477FB9; padding: 0 16px;font-size: 14px">默认船舶</span>
                        </div>
                    </div>
                    <div class="buttons" slot="right">
                        <mu-button class="button" style="background-color: #673ab7" @click="handlerDefault(item)" flat>
                            设为默认
                        </mu-button>
                        <mu-button class="button" style="background-color: #3f51b5" @click="handlerEdit(item)" flat>修改
                        </mu-button>
                        <mu-button class="button" style="background-color: #f44336" @click="handlerDelete(item)" flat>
                            删除
                        </mu-button>
                    </div>
                </swipe-cell>
                <div class="form-add" v-if="data.shipList.length < 5">
                    <mu-button class="button" color="#9F9F9F" flat @click="handlerAdd">
                        <mu-icon size="24" value="add"></mu-icon>
                        新增船舶
                    </mu-button>
                </div>
            </div>
            <div class="form">
                <div class="form-item border-1px-b">
                    <div class="form-item-label">手机号码</div>
                    <div class="form-item-content"><span>{{user.phone}}</span></div>
                    <div class="form-item-other">
                        <mu-button color="#477FB9" @click="handlerSavePhone" flat>
                            {{data.phone ? '修改' : '绑定'}}
                        </mu-button>
                    </div>
                </div>
                <mu-list style="padding: 0">
                    <mu-list-item class="list-item border-1px-b" to="/about/data/password" button>
                        <mu-list-item-title class="item-title">修改密码</mu-list-item-title>
                        <mu-list-item-action>
                            <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                        </mu-list-item-action>
                    </mu-list-item>
                </mu-list>
            </div>
            <div class="form-button">
                <mu-button class="button" color="#fff"
                           @click="handlerExit()"
                           flat>切换用户
                </mu-button>
                <mu-button @click="handlerExit('out')" color="#999" style="width: 100%" flat>注销</mu-button>
            </div>
        </div>

        <!-- 头像裁剪 -->
        <div style=" position: absolute; top: -10px; right: -10px; width: 0; height: 0; overflow: hidden;">
            <input type="file" ref="file">
        </div>
        <cropper v-model="cropper.visible" :img="cropper.avatar" v-show="cropper.visible"
                 @close="handlerCropperClose"
                 @confirm="handlerCropperConfirm"/>
        <!-- 头像裁剪 -->

        <!-- Router -->
        <transition name="views-left">
            <keep-alive>
                <router-view class="views" @update="handlerUpdate"/>
            </keep-alive>
        </transition>
        <!-- Router -->
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import Cropper from '../../../components/cropper/cropper'
    import SwipeCell from 'vant/lib/swipe-cell'
    import 'vant/lib/swipe-cell/style'
    import {loginOut} from "../../../api/user"
    import {queryIPersonalDatum, updateUserPortrait, deleteIShip, updateIDefaultShip} from "../../../api/personal"
    import {uploadBase64} from "../../../api/sys"
    import {userMixins} from "../../../common/mixins"

    export default {
        name: "mineData",
        mixins: [userMixins],
        data() {
            return {
                open: false,
                data: {
                    phone: '',
                    alias: '',
                    portrait: '',
                    shipList: []
                },
                shipForm: {
                    length: null,
                    width: null,
                    draft: null,
                    mmsi: "",
                    name: ""
                },
                cropper: {
                    avatar: null,
                    visible: false
                }
            }
        },
        activated() {
            this.queryIPersonalDatum()
        },
        methods: {
            handlerUpdate() {
                this.queryIPersonalDatum()
            },
            /***
             * 获取用户信息
             */
            queryIPersonalDatum() {
                queryIPersonalDatum().then(res => {
                    this.data = res
                }).catch(error => {
                    console.log(error)
                })
            },
            // 退出登录
            handlerExit(type) {
                this.$confirm(`您确认要${type === 'out' ? '退出登录' : '切换用户'}吗？`, '提示', {
                    type: 'warning'
                }).then(({result}) => {
                    if (result) {
                        loginOut()

                        this.logout()

                        if (type === 'out') {
                            this.$router.replace('/')
                        } else {
                            this.$router.replace('/login')
                        }
                    } else {
                        this.$toast('取消操作')
                    }
                })
            },
            // 填写
            handlerSave(target, prompt, validator) {
                this.$prompt(prompt, '提示', {
                    validator
                }).then(({result, value}) => {
                    if (result) {
                        this.shipForm[target] = value
                    } else {
                        this.$toast({
                            message: '取消操作',
                            zIndex: 20142000
                        })
                    }
                })
            },
            //  选择头像
            handlerClickAvatar() {
                this.$refs.file.addEventListener('change', event => {
                    this.$toast({
                        duration: 0,
                        message: '解析中 ...'
                    })
                    let file = event.target.files[0]

                    if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(event.target.value)) {
                        this.$toast('图片类型必须是.gif,jpeg,jpg,png,bmp中的一种')
                        return false
                    }

                    const reader = new FileReader()

                    reader.onload = e => {
                        let data
                        if (typeof e.target.result === 'object') {
                            // 把Array Buffer转化为blob 如果是base64不需要
                            data = window.URL.createObjectURL(new Blob([e.target.result]))
                        } else {
                            data = e.target.result
                        }

                        this.cropper.avatar = data

                        this.cropper.visible = true
                        this.$toast('解析完成')
                    }
                    // 转化为base64
                    // reader.readAsDataURL(file)
                    // 转化为blob
                    reader.readAsArrayBuffer(file)
                })

                this.$refs.file.click()
            },
            // 取消
            handlerCropperClose() {
                this.$refs.file.value = null
                this.cropper.avatar = null
            },
            // 上传头像
            handlerCropperConfirm(base64) {
                this.$toast({
                    type: 'loading',
                    position: 'center',
                    message: '正在上传图片 ...',
                    duration: 0
                })

                uploadBase64(base64).then(res => {
                    this.data.portrait = res
                    this.cropper.avatar = res

                    updateUserPortrait(res).then(res => {
                        this.$toast.clear()

                        this.login(Object.assign({}, this.user, {portrait: this.data.portrait}))
                    })
                })
            },
            //取消表单填写页
            handlerCancel() {
                this.open = false

                this.shipForm = {
                    id: "",
                    length: null,
                    width: null,
                    draft: null,
                    mmsi: "",
                    name: ""
                }
            },
            //显示表单填写页
            handlerAdd() {
                this.$router.push('/about/data/ship')
            },
            handlerEdit(item) {
                this.$router.push({
                    name: 'about-data-ship',
                    params: item
                })
            },
            handlerDefault(item) {
                updateIDefaultShip(item.id).then(res => {
                    this.queryIPersonalDatum()
                }).catch(error => {
                    console.log(error)
                })
            },
            handlerDelete(item) {
                this.$confirm(`您确认要删除船舶吗？`, '提示', {
                    type: 'warning'
                }).then(({result}) => {
                    if (result) {
                        deleteIShip(item.id).then(res => {
                            this.data.shipList.splice(this.data.shipList.findIndex(ship => {
                                return item.id === ship.id
                            }), 1)

                            this.$toast('删除成功')
                        }).catch(console.log)
                    } else {
                        this.$toast('取消操作')
                    }
                })

            },
            handlerSavePhone() {
                this.$router.push({
                    path: '/about/data/phone',
                    query: {
                        phone: this.data.phone
                    }
                })
            },
            handlerSaveInfo() {
                this.$router.push({
                    path: '/about/data/info',
                    query: {
                        alias: this.data.alias
                    }
                })
            }
        },
        components: {
            Navigate,
            Cropper,
            SwipeCell
        }
    }
</script>

<style lang="stylus">
    @import "../../../common/stylus/variable.styl"

    .about-data
        padding-top 46px
        background-color #EFEFEF
        .scroll-wrapper
            width 100%
            height 100%
            .form
                margin-bottom 20px
                background-color #fff
                .form-item
                    display flex
                    align-items center
                    padding 0 15px
                    height 50px
                    .form-item-label
                        width 100px
                        color #9F9F9F
                    .form-item-content
                        flex 1
                        color #545454
                        font-size $font-size-ms
                    .form-item-other
                        position relative
                .buttons
                    display flex
                    width 100%
                    height 100%
                    .button
                        min-width auto
                        height 100%
                        color #fff
                        border-radius 0
                .form-add
                    height 50px
                    .button
                        width 100%
                        height 100%
                .item-title
                    color #9F9F9F
            .form-button
                margin 0 10px 20px
                .button
                    margin-bottom 15px
                    width 100%
                    background-color #3899d7

        .form-item-row
            height 50px
        .form-item-button
            display flex
            justify-content center
            align-items center
            .form-button
                margin 0 15px
                .save
                    background-color #477FB9
                    color #fff
                .cancel
                    background-color #FF3636
                    color #fff
                    text-align left

        .form-item-right
            text-align right
            color #477FB9
</style>



